@import "./config/fingo-colors";
@import "./Modules/sass/style";
@import "./config/fingo-config";
@import "./config/fingo-reset";
@import "./config/fingo-grid-system";
// @import ./main-carousel


.a11y {
  @extend %a11y; }

// // * ------------------------------------------------
// // * Reset Component
// // * ------------------------------------------------
// button
//   transition: all 0.2s ease-in-out
//   @extend %reset-button
// a
//   transition: all 0.2s ease-in-out
//
// body
//   font-family: 'Spoqa Han Sans', 'Sans-serif'
//   +size(100% auto)
//
//
// // * ------------------------------------------------
// // * Main page - Container
// // * ------------------------------------------------
// .container
//   +size(1240px auto)
//   +margin(0 auto)
//   +tablet
//     width: percentage($t-container-width/$bp-tablet)
//   +mobile
//     width: percentage($m-container-width/$bp-mobile)

$m-container-width: 640px;
$t-container-width: 964px;
$d-container-width: 1240px;

$bp-mobile: 720px;
$bp-tablet: 1024px;

// * --------------------------------
// * Main page - Header
// * --------------------------------
.main-header {
  @include size(100%);
  .ui-carousel {
    @include size(100%); }
  .ui-carousel-tablist {
    @include tablet {
      display: none; }
    @include mobile {
      display: none; } }
  .ui-carousel-tabpanel {
    @include size(100%);
    position: relative; }
  .ui-carousel-image {
    @include position(absolute, top 50%);
    @include size(100% auto);
    transform: translateY(-50%); }
  .ui-carousel-button-group {
    button {
      @include tablet {
        top: 80%; }
      @include mobile {
        top: 80%; } }
    .ui-carousel-prev-button {
      left: 30px;
      @include tablet {
        left: percentage(860px / 1024px); }
      @include mobile {
        left: percentage(500px / 720px); } }
    .ui-carousel-next-button {
      right: 30px;
      @include tablet {
        right: percentage(50px / 1024px); }
      @include mobile {
        right: percentage(40px / 720px); } } }
  .ui-carousel-mask {
    &::after {
      content: '';
      display: block;
      @include size(100% 100%);
      @include position(absolute, top 0 left 0 z-index 10);
      background: radial-gradient(transparent 60%, $fingo-bg-light 90%); } }

  .container {
    // +position(absolute, top 0 z-index 1000)
    height: 100%; }
  .main-header-carousel-contents {
    @include position(absolute, top 50% left 0 z-index 10000);
    @include margin(left 80px);
    color: $fingo-white;
    transform: translateY(-50%);
    width: 40%;
    text-align: left;
    @include mobile {
      @include margin(left 0);
      @include position(absolute, top 65%);
      width: 100%; }
    h3 {
      a {
        @include margin(bottom 20px);
        display: block;
        color: $fingo-white;
        font-size: 60px;
        font-weight: bold;
        line-height: 70px;
        &:hover {
          color: $fingo-mian-color; }
        @include tablet {
          line-height: normal;
          font-size: 42px; }
        @include mobile {
          line-height: normal;
          font-size: 42px; } } }
    p {
      color: $fingo-white;
      font-size: 16px;
      @include margin(bottom 20px); }

    > a {
      display: block;
      border: 1px solid $fingo-white;
      @include size(125px 30px);
      color: $fingo-white;
      font-size: 14px;
      line-height: 30px;
      text-align: center;
      &:hover {
        border: 1px solid $fingo-mian-color;
        background: $fingo-mian-color; } } } }


// * --------------------------------
// * Main page - Main
// * --------------------------------
.main-main {
  color: $fingo-white;
  a {
    @extend %reset-link;
    color: $fingo-white; }
  .ui-carousel {
    width: 100%;
    height: auto;
    @include tablet {
      width: percentage($t-container-width/$bp-tablet); }
    @include mobile {
      width: percentage($m-container-width/$bp-mobile);
      max-width: 500px;
      min-width: 300px; }
    .ui-carousel-tablist {
      display: none; }
    .ui-carousel-button-group {
      .ui-carousel-prev-button {
        @include tablet {
          left: -45px; }
        @include mobile {
          left: -45px; } }
      .ui-carousel-next-button {
        @include tablet {
          right: -45px; }
        @include mobile {
          right: -45px; } } }
    .ui-carousel-mask {
      @include tablet {
        width: 736px;
 }        // width: percentage(840px/$t-container-width)
      @include mobile {
        width: 100%; } }
    .ui-carousel-tabpanel {
      @include size(auto);
      position: relative;
      &:focus,
      &:active,
      &:hover {
        .hover {
          display: block; } }
      .ui-carousel-image {
        @include position(relative, z-index 1);
        @include size(232px 335px);
        @include mobile {
          @include size(100% auto); } }
      .hover {
        @include position(absolute, top 0 z-index 10);
        @include size(100%);
        @include padding(left 1.2em right 1.2em);
        display: none;
        cursor: pointer;
        background: rgba(#000000, 0.85);
        @include mobile {
          @include size(100% 100%); }
        h3 {
          @include margin(top 55px bottom 35px);
          @include padding(bottom 1em);
          border-bottom: 1px solid rgba($fingo-mian-color, 0.4);
          font-size: 19px;
          @include mobile {
            font-size: 30px; } }
        .stars {
          @include margin(bottom 35px); }
        button {
          @include margin(bottom 10px left auto right auto);
          @include size(100px 35px);
          line-height: 32px;
          font-size: 14px;
          display: block;
          color: $fingo-white;
          border: 1px solid currentColor;
          &:nth-of-type(1) {
            &.active {
              @include background(color $fingo-heart);
              border: 1px solid $fingo-heart; }
            &:hover {
              @include background(color $fingo-heart);
              border: 1px solid $fingo-heart; } }
          &:nth-of-type(2) {
            &.active {
              @include background(color #27569c);
              border: 1px solid #27569c; }
            &:hover {
              @include background(color #27569c);
              border: 1px solid #27569c; } } } } } } }


// * --------------------------------
// * Main page - Main Box Office
// * --------------------------------
.main-main {
  .main-box-office {
    @include size(100% auto);
    @include padding(bottom 150px);
    @include background(color $fingo-bg-dark);
    text-align: center;
    .ui-carousel-tabpanel-wrapper {
      position: relative;
      @include padding(top 55px);
      text-align: center;
      color: $fingo-white; }
    .ui-carousel-tabpanel {
      transition: transform 0.2s ease-in-out;
      @include mobile {
        @include size(auto);
        @include margin(right 20px); }
      .hover {
        @include size(100% calc(100% - 44px));
        @include mobile {
          @include size(100% calc(100% - 50px)); } }
      .ui-carousel-image {
        opacity: 0.5;
        @include mobile {
          opacity: 1; } }
      .box-office-movie-title {
        @include size(100% auto);
        @include margin(top 20px);
        font-size: 16px;
        white-space: nowrap;
        overflow: hidden;
        font-weight: normal;
        span {
          color: $fingo-mian-color; }
        @include mobile {
          font-size: 20px; } }
      &.active {
        @include position(relative, z-index 100);
        transform: scale(1.3);
        box-shadow: 0 0 30px 5px $fingo-bg-dark;
        .ui-carousel-image {
          opacity: 1; } } }

    h1 {
      @include padding(top 110px);
      font-size: 22px;
      position: relative;
      &::after {
        content: '';
        @include position(absolute, bottom -20px left 50%);
        @include size(50px 1px);
        transform: translateX(-50%);
        display: block;
        background: #454545; }
      @include mobile {
        @include padding(top 70px); } }
    .main-box-office-desc {
      @include margin(top 50px bottom 70px);
      font-weight: lighter;
      @include mobile {
        @include margin(bottom 80px); } }
    .main-box-office-list {
      @include size(300px 45px);
      @include position(absolute, top 150px right 0 z-index 10000);
      @include background(color $fingo-bg-dark);
      overflow: hidden;
      text-align: left;
      border: 1px solid $fingo-mian-color;
      border-radius: 5px;
      @include tablet {
        @include size(240px 45px); }
      @include mobile {
        @include position(absolute, top 200px left 50%);
        transform: translateX(-50%); }
      span,
      li a {
        @extend %reset-border-box;
        display: inline-block;
        line-height: 45px; }
      > span {
        @include size(95px 45px);
        @include padding(left 25px);
        font-size: 15px;
        color: #e8d0ad;
        vertical-align: top;
        @include tablet {
          @include size(70px 45px);
          @include padding(left 12px); } }
      button {
        @include size(40px);
        @include position(absolute, top 1px right 0 z-index 10);
        @include background(color $fingo-bg-dark);
        &:focus {
          @extend %focus-state; }
        &::before {
          content: '';
          @include position(absolute, top calc(50% - 2px) left calc(50% - 5px));
          @include size(0);
          border-left: 5px solid transparent;
          border-right: 5px solid transparent;
          border-top: 6px solid #fff; } }
      ul {
        @extend %reset-border-box;
        position: relative;
        width: 155px;
        display: inline-block;
        overflow: hidden;
        @include tablet {
          width: 120px; }
        a {
          @include padding(left 15px);
          width: 100%;
          font-size: 14px;
          white-space: nowrap;
          @include tablet {
            @include padding(left 10px); }
          span {
            @include padding(right 20px);
            width: auto;
            @include tablet {
              @include padding(right 10px); } }
          &:hover {
            background: hsla(35, 56%, 79%, 0.3); } } }
      &.show {
        height: auto;
        button {
          &::before {
            border-bottom: 6px solid #fff;
            border-top: 0; } }
        ul {
          width: 100%;
          a {
            @include size(100% 45px);
            @include padding(left 25px);
            span {
              width: 50px; } } } } } } }


// * --------------------------------
// * Main page - Main Movie Ranking
// * --------------------------------
.main-main {
  .main-movie-ranking {
    @include size(100% auto);
    @include padding(bottom 300px);
    @include background(color $fingo-bg-light);
    @include mobile {
      @include size(100% auto); }
    .ui-carousel-tabpanel {
      text-align: center;
      @include mobile {
        @include margin(right 20px); } }
    .main-movie-ranking-btn-group {
      @include margin(top 115px bottom 40px left auto right auto);
      @include tablet {
        width: 736px; }
      @include mobile {
        width: percentage(350px/720px); }
      button {
        @include size(232px 50px);
        @include margin(right 20px);
        color: $fingo-white;
        background: $fingo-bg-dark;
        @include mobile {
          @include margin(right 0 top 10px);
          @include size(100% 50px); }
        &:last-child {
          @include margin(right 0); }
        &.active {
          background: $fingo-mian-color; } } }
    .ui-carousel-tabpanel-wrapper {
      position: relative; } } }

// * ------------------------------------
// * Main page - Main New Movie Trailers
// * ------------------------------------
.main-main {
  .main-new-movie-trailers {
    @include size(100% 420px);
    @include background(color $fingo-bg-light);
    .ui-carousel-mask {
      @include tablet {
        @include size(358px 202px); }
      @include mobile {
        @include size(484px 197px); } }
    .ui-carousel-tabpanel {
      .ui-carousel-image {
        @include size(400px 225px);
        @include tablet {
          @include size(358px 202px); }
        @include mobile {
          @include size(484px 197px); } } }

    h1 {
      @include margin(bottom 40px);
      font-size: 22px; } } }
